.footer {
	overflow: hidden;
	margin-bottom: 16px;
	color: $white;

	@include media-breakpoint-up(sm) {
		margin-bottom: 30px;
	}

	@include media-breakpoint-up(lg) {
		margin-bottom: 50px;
	}

	@include media-breakpoint-up(xxl) {
		margin-bottom: 70px;
	}

	&__wrap {
		position: relative;
		z-index: 1;

		&:before {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			z-index: 1;
			content: '';
			display: block;
			pointer-events: none;
			border-radius: $border-radius-2xl;
			background: $gradient;

			@include media-breakpoint-up(md) {
				left: -15px;
				right: -15px;
			}

			@include min-w(1800) {
				left: -140px;
				right: -140px;
			}
		}

		&:after {
			@include absoluteCover;

			content: '';
			background-image: url("../img/footer-bg.svg");
			background-repeat: no-repeat;
			background-size: 790px;
			background-position: bottom 0 right -215px;
			z-index: 2;

			@include media-breakpoint-up(lg) {
				background-size: contain;
				background-position: center;
			}
		}
	}

	&__inner-content {
		position: relative;
		z-index: 5;
		padding: 0 25px;

		@include media-breakpoint-up(md) {
			padding: 0 30px;
		}

		@include min-w(1800) {
			padding: 0;
		}
	}

	&__top-content {
		padding: 45px 0 5px;

		@include media-breakpoint-up(lg) {
			padding: 50px 0 5px;
		}
	}

	&__logo {
		display: block;
		max-width: 140px;
		width: 100%;
		margin-bottom: 25px;

		@include media-breakpoint-up(md) {
			margin-bottom: 30px;
		}

		@include media-breakpoint-up(lg) {
			max-width: 150px;
			margin-bottom: 40px;
		}

		@include media-breakpoint-up(xxl) {
			max-width: 210px;
		}
	}

	&__desc {
		margin-bottom: 25px;

		@include media-breakpoint-up(md) {
			margin-bottom: 0;
		}

		@include media-breakpoint-up(lg) {
			margin-bottom: 30px;
		}
	}

	&__logo-desc-and-socials {
		margin-bottom: 25px;

		@include media-breakpoint-up(md) {
			margin-bottom: 65px;
		}

		@include media-breakpoint-up(lg) {
			margin-bottom: 0;
		}
	}

	&__bottom-content {
		position: relative;
		padding: 5px 0 25px;

		@include media-breakpoint-up(xs) {
			padding: 5px 30px 25px;
		}

		@include media-breakpoint-up(sm) {
			padding: 25px 0;
		}

		@include media-breakpoint-up(md) {
			padding: 45px 0;
		}

		@include media-breakpoint-up(lg) {
			padding: 25px 0;
		}

		&:after {
			@include media-breakpoint-up(lg) {
				content: '';
				position: absolute;
				top: 0;
				left: -100vw;
				width: 300vw;
				height: 1px;
				background-color: rgba($white, 0.2);
			}
		}
	}
}

.footer-nav {
	@include listreset;

	&__item {
		&:not(:first-child) {
			margin-top: 15px;

			@include media-breakpoint-up(md) {
				margin-top: 35px;
			}
		}
	}

	&__link {
		position: relative;
		color: $white;
		transition: color 0.3s;

		@include media-breakpoint-down(sm) {
			font-size: 14px;
		}

		&:after {
			content: "";
			position: absolute;
			bottom: -3px;
			left: 0;
			width: 100%;
			height: 2px;
			background: $gradient;
			transform: scaleX(0);
			transform-origin: right center;
			transition: transform .5s cubic-bezier(.694,0,.335,1);
		}

		&:hover {
			color: $white;

			&:after {
				transform: scaleX(1);
				transform-origin: left center;
			}
		}
	}
}

.footer-title-and-nav {
	margin-bottom: 20px;
	padding: 0 15px 20px 0;
	width: 100%;
	border-bottom: 1px solid rgba($white, 0.2);

	@include media-breakpoint-up(sm) {
		margin-bottom: 30px;
		padding: 0;
		border-bottom: none;
	}

	&__title {
		@include media-breakpoint-down(lg) {
			font-size: 18px;
		}

		@include media-breakpoint-down(md) {
			font-size: 16px;
		}

		@include media-breakpoint-up(md) {
			margin-bottom: 35px;
		}
	}
}