.our-team-round-block {
	position: relative;

	@include media-breakpoint-up(xxl) {
		width: 860px;
	}

	&__photo-item {
		@include sizeElement(115px);

		position: absolute;
		display: block;
		border-radius: 50%;
		transition: background-color 0.3s;

		&:before {
			@include absoluteCover;

			content: '';
			display: block;
			background: $gradient;
			border-radius: 50%;
			transition: opacity 0.3s;
			opacity: 0;
			z-index: 4;
		}

		&:hover {
			background-color: rgba($secondary, 0.2);
		}

		&.active {
			&:before {
				opacity: 1;
			}
		}

		&.top {
			top: -6%;
			left: 40%;
		}

		&.top-left {
			top: 10%;
			left: 6%;
		}

		&.left {
			left: 0;
			top: 50%;
			transform: translate(-50%, -50%);
		}

		&.bottom-left {
			bottom: 10%;
			left: 6%;
		}

		&.bottom {
			bottom: -6%;
			left: 40%;
		}
	}

	&__photo-item-inner {
		position: absolute;
		top: 10px;
		left: 10px;
		right: 10px;
		bottom: 10px;
		z-index: 5;
		border-radius: 50%;
		overflow: hidden;
	}
}