.google-map {
	@include absoluteCover;

	background-color: rgba($gray-400, 0.5);

	// hide bottom labels
	.gm-style-cc {
		display: none;
	}

	// hide google label
	a[href^="http://maps.google.com/maps"],
	a[href^="https://maps.google.com/maps"],
	a[href^="https://www.google.com/maps"] {
		display: none !important;
	}
}