body {
	min-width: 320px;
}

.main {
	overflow: hidden;

	> * {
		&:not(.section-default) {
			&:first-child {
				padding-top: 0;
			}
		}

		&.section-welcome-about-wrap {
			> * {
				&:first-child {
					padding-top: 0;
				}
			}
		}
	}
}

.container,
.container-fluid,
.container-xxl,
.container-xl,
.container-lg,
.container-md,
.container-sm {
	padding-left: 16px;
	padding-right: 16px;

	@include media-breakpoint-up(md-extra) {
		padding-left: 32px;
		padding-right: 32px;
	}

	@include media-breakpoint-down(md-extra) {
		max-width: map-get($container-max-widths, "md-extra");
	}
}

.row {
	@include media-breakpoint-down(lg) {
		margin-left: -8px;
		margin-right: -8px;
	}

	@include media-breakpoint-down(md-extra) {
		margin-left: -7px;
		margin-right: -7px;
	}

	[class^="col"],
	.col {
		@include media-breakpoint-down(lg) {
			padding-left: 8px;
			padding-right: 8px;
		}

		@include media-breakpoint-down(md-extra) {
			padding-left: 7px;
			padding-right: 7px;
		}
	}
}

::selection {
	background-color: $primary;
	color: $dark;
}