.card {
	&-about {
		background-color: rgba($white, 0.78);
		border: 2px solid $white;
		box-shadow: $box-shadow;
		backdrop-filter: blur(7px);
		-webkit-backdrop-filter: blur(7px);
		padding: 20px 15px;
		width: 100%;
		text-align: center;
		margin-bottom: 12px;

		@include media-breakpoint-up(md) {
			padding: 40px 20px;
		}

		@include media-breakpoint-up(lg) {
			padding: 32px 32px 60px;
			text-align: left;
		}

		&.horizontal-on-mobile {
			@include media-breakpoint-down(sm) {
				display: flex;
				align-items: center;
				text-align: left;
			}

			.card-about__icon {
				@include media-breakpoint-down(sm) {
					margin: 0;
					flex: 0 0 auto;
					max-width: 100%;
				}
			}

			.card-about__body {
				@include media-breakpoint-down(sm) {
					flex: 1 0 0%;
					width: 100%;
					max-width: 100%;
					padding-left: 35px;
				}
			}
		}

		&__icon {
			@include sizeElement(80px);
			@include flexCenter;

			margin: 0 auto 25px;

			@include media-breakpoint-up(xs) {
				@include sizeElement(120px);
			}

			@include media-breakpoint-up(lg) {
				@include sizeElement(100px);

				margin: 0 0 20px 0;
			}

			img {
				flex-shrink: 0;
				max-height: 100%;
			}
		}

		&__title {
			font-size: 18px;
			margin-bottom: 10px;
			font-family: inherit;

			@include media-breakpoint-up(xs) {
				font-size: 22px;
				margin-bottom: 20px;
			}
		}

		&__text {
			@include media-breakpoint-down(sm) {
				font-size: 12px !important;
			}
		}
	}

	&-fair {
		background-color: rgba($white, 0.2);
		padding: 25px 15px;
		box-shadow: $box-shadow;
		width: 100%;
		margin-bottom: 12px;

		@include media-breakpoint-up(sm) {
			padding: 30px 20px;
			margin-bottom: 16px;
		}

		@include media-breakpoint-up(xxl) {
			padding: 80px 65px 45px;
		}

		&__icon {
			position: relative;
			overflow: hidden;
			margin-bottom: 10px;
			width: 100px;
			height: 70px;

			@include media-breakpoint-up(md) {
				width: 130px;
				height: 100px;
				margin-bottom: 25px;
			}

			@include media-breakpoint-up(xxl) {
				width: 200px;
				height: 170px;
			}
		}

		&__title {
			color: $white;
			margin-bottom: 10px;

			@include media-breakpoint-up(sm) {
				margin-bottom: 5px;
			}
		}

		&__text {
			color: rgba($white, 0.6);

			@include media-breakpoint-down(sm) {
				font-size: 12px !important;
			}
		}
	}

	&-digital-network {
		display: flex;
		position: relative;
		padding-top: 25px;

		&:before {
			@include sizeElement(75px);

			content: '';
			position: absolute;
			top: 10px;
			left: 50%;
			transform: translate(-50%, 0) rotate(45deg);
			border-radius: 12px;
			background-color: $white;
			box-shadow: $box-shadow-sm;
		}

		&__wrap {
			display: flex;
			box-shadow: $box-shadow-sm;
			border-radius: $border-radius-lg;
			width: 100%;
		}

		&__main-content {
			position: relative;
			z-index: 5;
			border-radius: $border-radius-lg;
			background-color: $white;
			padding: 25px;
			width: 100%;

			@include media-breakpoint-up(xl) {
				padding: 25px 30px 40px;
			}
		}

		&__img {
			@include responsive-ratio(1, 1);

			position: relative;
			max-width: 160px;
			margin: 0 auto 15px;

			@include media-breakpoint-up(xl) {
				max-width: 100%;
				margin-bottom: 30px;
			}
		}

		&__title {
			margin-bottom: 0;
		}
	}

	&-with-dot {
		display: flex !important;
		flex-direction: column;

		&__dot {
			@include sizeElement(50px);

			position: relative;
			border-radius: 50%;
			background-color: $white;
			box-shadow: $box-shadow-sm;
			margin: 0 auto 15px;

			@include media-breakpoint-up(lg) {
				margin-bottom: 30px;
			}

			@include media-breakpoint-up(xxl) {
				@include sizeElement(72px);
			}

			&:after {
				@include sizeElement(24px);
				@include absoluteCenter;

				@include media-breakpoint-up(xxl) {
					@include sizeElement(34px);
				}

				content: '';
				display: block;
				border-radius: 50%;
				background-color: $blue;
			}
		}

		&__card {
			flex-grow: 1;
			cursor: pointer;
		}
	}

	&-contact {
		background-color: $white;
		border: 1px dashed rgba($black, 0.1);
		padding: 30px 15px;
		border-radius: $border-radius-lg;
		text-align: center;
		width: 100%;
		margin-bottom: 15px;

		@include media-breakpoint-up(sm) {
			margin-bottom: 40px;
		}

		@include media-breakpoint-up(xl) {
			padding: 30px 25px;
		}

		&__icon {
			@include sizeElement(48px);
			@include flexCenter;

			margin: 0 auto 25px;
			border-radius: 50%;
			background-color: rgba($primary, 0.15);
			font-size: 24px;

			> * {
				background: $gradient;
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;
			}
		}

		&__title {
			color: $secondary;
			margin-bottom: 5px;
		}

		&__link {
			color: $dark;
			transition: color 0.3s;

			&:hover {
				color: $primary;
			}
		}
	}

	&-solution {
		background-color: rgba($white, 0.8);
		backdrop-filter: blur(32px);
		-webkit-backdrop-filter: blur(32px);
		box-shadow: $box-shadow-sm;
		padding: 30px 12px;
		width: 100%;
		margin-bottom: 16px;

		@include media-breakpoint-up(md) {
			padding: 50px;
		}

		&__wrap {
			position: relative;
			z-index: 5;
		}

		&__icon {
			@include responsive-ratio(140, 170);

			position: relative;
			max-width: 140px;
			width: 100%;
			margin: 0 auto 25px;
		}

		&__text {
			@include media-breakpoint-down(sm) {
				font-size: 12px !important;
			}
		}
	}

	&-benefit {
		background-color: rgba($white, 0.7);
		backdrop-filter: blur(32px);
		-webkit-backdrop-filter: blur(32px);
		padding: 25px 25px 45px;
		box-shadow: $box-shadow-sm;
		width: 100%;
		margin-bottom: 16px;

		@include media-breakpoint-up(xl) {
			padding: 70px;
		}

		&__icon {
			@include responsive-ratio(260, 200);

			position: relative;
			max-width: 200px;
			margin: 0 auto 35px;

			@include media-breakpoint-up(lg) {
				max-width: 260px;
			}
		}

		&__text {
			@include media-breakpoint-down(md) {
				font-size: 12px !important;
			}
		}
	}

	&-functionality {
		display: flex;
		align-items: center;
		background-color: rgba($white, 0.7);
		box-shadow: $box-shadow-sm;
		backdrop-filter: blur(32px);
		-webkit-backdrop-filter: blur(32px);
		padding: 35px 30px;
		margin-bottom: 12px;
		width: 100%;

		@include media-breakpoint-up(md) {
			margin-bottom: 25px;
		}

		&__icon {
			@include responsive-ratio(1, 1);

			position: relative;
			width: 75px;
		}

		&__body {
			flex: 1 0 0%;
			width: 100%;
			max-width: 100%;
			padding-left: 15px;

			@include media-breakpoint-up(lg) {
				padding-left: 30px;
			}
		}

		&__title {
			font-weight: 400;
			font-size: 16px;
			line-height: 1.54;
			font-family: inherit;
			margin-bottom: 0;

			@include media-breakpoint-up(lg) {
				font-size: 22px;
			}
		}
	}
}