.countdown-block {
	position: relative;
	z-index: 5;

	&:after {
		@include media-breakpoint-up(sm) {
			content: '';
			display: block;
			background: $gradient;
			opacity: 0.6;
			height: 70%;
			position: absolute;
			left: 35px;
			right: 35px;
			left: 35px;
			bottom: -10px;
			filter: blur(58px);
			border-radius: 18px;
			z-index: -1;
			pointer-events: none;
		}

		@include media-breakpoint-up(md) {
			bottom: -40px;
		}

		@include media-breakpoint-up(xl) {
			width: 70%;
			left: 65px;
			bottom: -30px;
			right: auto;
		}
	}

	&__wrap {
		position: relative;
		z-index: 3;
		background: $gradient;
		padding: 15px 25px 25px;
		border-radius: $border-radius-sm;
		color: $white;

		@include media-breakpoint-up(sm) {
			display: flex;
			align-items: center;
		}

		@include media-breakpoint-up(md) {
			padding: 25px;
		}

		@include media-breakpoint-up(xl) {
			padding: 30px;
		}

		@include media-breakpoint-up(xxl) {
			padding: 30px 50px 40px;
		}
	}

	&__main-block {
		flex-grow: 1;

		@include media-breakpoint-up(sm) {
			max-width: 330px;
			padding-right: 50px;
		}

		@include media-breakpoint-up(xl) {
			max-width: 100%;
			padding-right: 30px;
		}

		@include media-breakpoint-up(xxl) {
			padding-right: 65px;
		}
	}

	&__btn-block {
		flex-shrink: 0;
		margin-top: 15px;

		@include media-breakpoint-up(sm) {
			margin-top: 0;
		}
	}

	&__btn {
		@include media-breakpoint-down(sm) {
			width: 100%;
			min-height: 48px !important;
			padding-top: 10px;
			padding-bottom: 10px;
		}
	}
}