.header {
	padding: 15px 0;
	transition: padding 0.5s, background-color 0.5s;

	@include media-breakpoint-up(md) {
		padding: 25px 0;
	}

	@include media-breakpoint-up(xl) {
		padding: 40px 0;
	}

	@include media-breakpoint-up(xxl) {
		padding: 55px 0;
	}

	&.fixed-top {
		+ .main {
			padding-top: 85px;

			@include media-breakpoint-up(md) {
				padding-top: 120px;
			}

			@include media-breakpoint-up(lg) {
				padding-top: 180px;
			}

			@include media-breakpoint-up(xxl) {
				padding-top: 220px;
			}
		}
	}

	&.active {
		padding: 10px 0;
		background-color: $white;

		@include media-breakpoint-up(xxl) {
			padding: 20px 0;
		}
	}

	&__main-content {
		display: flex;
		align-items: center;
		justify-content: space-between;

		@include media-breakpoint-up(md) {
			margin: 0 -15px;
		}

		@include media-breakpoint-up(lg) {
			margin: 0;
		}
	}

	&__logo {
		display: block;
		max-width: 95px;
		width: 100%;
		margin-right: 30px;

		@include media-breakpoint-up(md) {
			max-width: 140px;
		}

		@include media-breakpoint-up(lg) {
			max-width: 180px;
		}

		@include media-breakpoint-up(xxl) {
			max-width: 210px;
		}
	}

	&__menu-and-hamburger {
		@include media-breakpoint-up(md) {
			margin-top: 4px;
		}
	}
}

.header-nav {
	@include listreset;

	@include media-breakpoint-up(md) {
		display: flex;
		justify-content: end;
		flex-wrap: wrap;
	}

	&__item {
		margin-top: 10px;
		margin-bottom: 10px;

		@include media-breakpoint-up(md) {
			margin-top: 3px;
			margin-bottom: 3px;
		}

		&:not(:first-child) {
			@include media-breakpoint-up(md) {
				margin-left: 50px;
			}

			@include media-breakpoint-up(lg) {
				margin-left: 65px;
			}
		}
	}

	&__link {
		position: relative;
		display: inline-block;
		color: $secondary;
		font-size: 16px;
		line-height: 1.1;
		transition: color 0.3s;

		@include media-breakpoint-up(lg) {
			font-size: 20px;
		}

		@include media-breakpoint-up(xxl) {
			font-size: $font-size-xl;
		}

		&:after {
			content: "";
			position: absolute;
			bottom: -3px;
			left: 0;
			width: 100%;
			height: 2px;
			background: $gradient;
			transform: scaleX(0);
			transform-origin: right center;
			transition: transform .5s cubic-bezier(.694,0,.335,1);
		}

		&:hover {
			color: $dark;

			&:after {
				transform: scaleX(1);
				transform-origin: left center;
			}
		}
	}
}