.social-list {
	@include listreset;

	display: flex;
	flex-wrap: wrap;

	&.justify-content-center {
		.social-list__item {
			margin-left: 8px;
			margin-right: 8px;
		}
	}

	&.justify-content-sm-start {
		@include media-breakpoint-up(sm) {
			margin-left: -8px;
			margin-right: 0;
		}
	}

	&.justify-content-md-end {
		@include media-breakpoint-up(md) {
			margin-left: 0;
			margin-right: -8px;
		}
	}

	&.justify-content-lg-start {
		@include media-breakpoint-up(lg) {
			margin-left: -8px;
			margin-right: 0;
		}
	}

	&__item {
		margin-bottom: 16px;

		&:not(:last-child) {
			margin-right: 16px;
		}
	}

	&__link {
		@include sizeElement(40px);
		@include inlineFlexCenter;

		position: relative;
		border-radius: 50%;
		border-width: 2px;
		border-style: solid;
		font-size: 18px;
		transition: border-color 0.3s;

		@include media-breakpoint-up(xl) {
			@include sizeElement(52px);

			font-size: 24px;
		}
	}

	&.white {
		.social-list__link {
			border-color: rgba($white, 0.25);
			color: $white;

			&:hover {
				border-color: $white;
			}
		}
	}

	&.primary {
		.social-list__link {
			border-color: rgba($black, 0.05);
			color: $primary;

			&:hover {
				border-color: $primary;
			}
		}
	}
}