.btn {
	display: inline-flex;
	align-items: center;
	justify-content: center;

	&:not(.btn-sm):not(.btn-lg) {
		min-height: 52px;
		min-width: 200px;

		@include media-breakpoint-up(md) {
			min-height: 64px;
			min-width: 220px;
		}

		@include media-breakpoint-up(xxl) {
			min-height: 78px;
			min-width: 255px;
		}

		@include media-breakpoint-down(xxl) {
			font-size: 18px;
		}
	}

	&[disabled],
	&:disabled {
		background-color: $gray-100 !important;
		border-color: $gray-100 !important;
		color: $gray-300 !important;
		pointer-events: none;

		&:before,
		&:after {
			display: none !important;
		}
	}

	&-light {
		color: $green-light;

		&:hover {
			color: $green-light;
		}
	}

	&-gradient {
		position: relative;
		color: $white;
		overflow: hidden;

		&:before,
		&:after {
			@include absoluteCover;

			content: '';
			display: block;
			pointer-events: none;
			border-radius: $btn-border-radius;
		}

		&:before {
			background: $gradient;
		}

		&:after {
			background: $gradient-hover;
			opacity: 0;
			transition: opacity 0.3s;
		}

		> * {
			position: relative;
			z-index: 5;
			display: inline-block;
		}

		&:hover {
			color: $white;

			&:after {
				opacity: 1;
			}
		}

		&:active,
		&:visited {
			color: $white;
		}
	}
}