.tabs-list {
	@include listreset;

	position: relative;
	display: flex;
	width: 100%;
	background-color: $white;
	border-radius: $btn-border-radius;

	@include media-breakpoint-up(sm) {
		display: inline-flex;
		width: auto;
	}

	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		display: block;
		width: 50%;
		height: 100%;
		background: $gradient;
		border-radius: $btn-border-radius;
		transition: transform 0.5s;

		@include media-breakpoint-up(sm) {
			width: 170px;
		}

		@include media-breakpoint-up(xxl) {
			width: 210px;
		}
	}

	&[data-active="1"] {
		&:after {
			transform: translate(0, 0);
		}
	}

	&[data-active="2"] {
		&:after {
			transform: translate(100%, 0);
		}
	}

	&[data-active="3"] {
		&:after {
			transform: translate(200%, 0);
		}
	}

	&__item {
		position: relative;
		z-index: 5;
		width: 50%;

		@include media-breakpoint-up(sm) {
			width: auto;
		}
	}

	&__btn {
		min-width: 100% !important;
		width: 100%;
		color: $dark;

		@include media-breakpoint-up(sm) {
			width: 170px;
		}

		@include media-breakpoint-up(xxl) {
			width: 210px;
		}

		&.active {
			color: $white;
			cursor: default;
		}

		&:not(.active) {
			&:hover {
				color: $secondary;
			}
		}
	}
}