.slider {
	&-digital-network {
		position: relative;
		max-width: 228px;

		@include media-breakpoint-up(sm) {
			max-width: 466px;
		}

		@include media-breakpoint-up(md-extra) {
			max-width: 1412px;
		}

		&:before {
			content: '';
			position: absolute;
			top: 18px;
			left: -100vw;
			width: 300vw;
			height: 15px;
			background-color: $white;
			display: block;

			@include media-breakpoint-up(xxl) {
				top: 25px;
				height: 20px;
			}
		}

		.tns-ovh {
			overflow: inherit;
		}

		.tns-slider {
			display: flex;
			flex-wrap: wrap;
		}

		.tns-item {
			display: flex;
			width: 100%;
		}
	}

	&-our-team {
		.tns-controls {
			padding: 40px 25px 0 25px;
			border-top: 1px solid rgba($secondary, 0.1);
			margin: 50px -50px 0;

			[data-controls="prev"],
			[data-controls="next"] {
				@include sizeElement(62px);
				@include inlineFlexCenter;

				position: relative;
				border-radius: 50%;
				border: none;
				color: $white;
				padding: 10px;
				font-size: 20px;
				transition: color 0.3s;

				&:before {
					@include absoluteCover;

					content: '';
					display: block;
					background: $gradient-hover;
					border-radius: 50%;
					z-index: 1;
					transition: opacity 0.3s;
				}

				&:after {
					@include absoluteCover;

					content: '';
					display: block;
					background: $gradient;
					border-radius: 50%;
					z-index: 2;
					transition: opacity 0.3s;
				}

				> * {
					position: relative;
					z-index: 5;
				}

				&[disabled] {
					background-color: rgba($secondary, 0.1);
					color: $blue;
					pointer-events: none;

					&:before,
					&:after {
						opacity: 0;
					}
				}

				&:hover {
					&:after {
						opacity: 0;
					}
				}
			}

			[data-controls="prev"] {
				margin-right: 15px;
			}
		}
	}
}