.section {
	padding: 50px 0;

	@include media-breakpoint-up(md) {
		padding: 90px 0;
	}

	&-welcome-about-wrap {
		position: relative;
		z-index: 5;

		@include media-breakpoint-down(sm) {
			background-image: none !important;
		}

		@include media-breakpoint-up(sm) {
			background-repeat: no-repeat;
			background-size: 600px;
			background-position: top center;
		}

		@include media-breakpoint-up(md) {
			background-size: 835px;
		}

		@include media-breakpoint-up(md-extra) {
			background-size: 115%;
		}

		@include media-breakpoint-up(lg) {
			background-position: top 50px center;
			background-size: 98%;
		}

		@include media-breakpoint-up(xxl) {
			background-position: top center;
		}
	}

	&-welcome {
		padding: 20px 0;

		@include media-breakpoint-up(sm) {
			padding: 40px 0;
		}

		@include media-breakpoint-up(md) {
			padding: 40px 0 115px;
		}

		@include media-breakpoint-up(md-extra) {
			padding: 40px 0;
		}

		@include media-breakpoint-up(lg) {
			padding: 80px 0;
		}

		&__title-holder {
			margin-bottom: 10px;

			@include media-breakpoint-up(sm) {
				margin-bottom: 100px;
			}

			@include media-breakpoint-up(md) {
				margin-bottom: 0;
			}
		}

		&__title {
			@include media-breakpoint-up(md) {
				margin-bottom: 100px;
			}

			@include media-breakpoint-up(lg) {
				margin-bottom: 60px;
			}

			@include media-breakpoint-up(xxl) {
				margin-bottom: 80px;
			}
		}

		&__img {
			margin-bottom: 15px;
			margin-left: -16px;
			margin-right: -16px;
		}

		&__countdown {
			max-width: 600px;

			@include media-breakpoint-up(xl) {
				max-width: 680px;
			}

			@include media-breakpoint-up(xxl) {
				max-width: 860px;
			}
		}
	}

	&-about {
		padding: 20px 0 40px;

		@include media-breakpoint-down(lg) {
			background-image: none !important;
		}

		@include media-breakpoint-up(sm) {
			padding: 40px 0 40px;
		}

		@include media-breakpoint-up(md) {
			padding-top: 115px;
		}

		@include media-breakpoint-up(md-extra) {
			padding-top: 40px;
		}

		@include media-breakpoint-up(lg) {
			padding-top: 80px;
			background-repeat: no-repeat;
			background-size: 30%;
			background-position: right -5% bottom 50px;
		}

		&__title-holder {
			margin-bottom: 20px;

			@include media-breakpoint-up(sm) {
				margin-bottom: 25px;
				padding-right: 0;
			}

			@include media-breakpoint-up(md-extra) {
				margin-bottom: 60px;
			}

			@include media-breakpoint-up(xxl) {
				margin-bottom: 85px;
			}
		}

		&__text {
			@include media-breakpoint-up(xs) {
				padding-right: 50px;
			}

			@include media-breakpoint-up(sm) {
				padding-right: 0;
			}
		}
	}

	&-fair {
		position: relative;
		overflow: hidden;
		padding: 460px 0 45px;
		background: $gradient;
		color: $white;
		margin-top: -480px;
		z-index: 1;

		@include media-breakpoint-up(xs) {
			margin-top: -580px;
			padding-top: 570px;
		}

		@include media-breakpoint-up(sm) {
			padding-top: 390px;
			margin-top: -370px;
		}

		@include media-breakpoint-up(md) {
			padding-bottom: 100px;
		}

		@include media-breakpoint-up(md-extra) {
			padding-top: 275px;
			margin-top: -250px;
		}

		@include media-breakpoint-up(xxl) {
			padding-bottom: 140px;
		}

		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			border-top: 150px solid $body-bg;
			border-right: 100vw solid transparent;

			@include media-breakpoint-up(sm) {
				border-top-width: 300px;
			}

			@include media-breakpoint-up(md-extra) {
				border-top-width: 200px;
			}
		}

		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			display: block;
			height: 1px;
			width: 100%;
			background-color: $body-bg;
			z-index: 5;
			pointer-events: none;
		}

		&__wrap {
			position: relative;
			z-index: 5;
		}

		&__title {
			color: inherit;

			@include media-breakpoint-up(xs) {
				margin-bottom: 0;
			}

			@include media-breakpoint-up(lg) {
				margin-bottom: 65px;
			}
		}
	}

	&-digital-network {
		padding: 40px 0;
		overflow: hidden;

		@include media-breakpoint-down(md-extra) {
			background-image: none !important;
		}

		@include media-breakpoint-up(sm) {
			padding-bottom: 30px;
		}

		@include media-breakpoint-up(md-extra) {
			padding: 80px 0;
			background-repeat: no-repeat;
			background-size: 25%;
			background-position: right 30% bottom 0;
		}

		@include media-breakpoint-up(lg) {
			padding: 100px 0;
		}

		@include media-breakpoint-up(xxl) {
			padding: 160px 0 120px;
		}

		&__wrap {
			position: relative;
			z-index: 5;
		}

		&__title-holder {
			margin-bottom: 50px;

			@include media-breakpoint-up(xl) {
				margin-bottom: 80px;
			}

			@include media-breakpoint-up(xxl) {
				margin-bottom: 110px;
			}
		}
	}

	&-become-part {
		position: relative;

		@include media-breakpoint-down(md-extra) {
			background-image: none !important;
		}

		@include media-breakpoint-up(sm) {
			margin-top: -50px;
		}

		@include media-breakpoint-up(md-extra) {
			padding-top: 100px;
			margin-top: -100px;
			background-repeat: no-repeat;
			background-position: top;
			background-size: contain;
		}

		@include media-breakpoint-up(xl) {
			padding-top: 120px;
			margin-top: -120px;
		}

		&:after {
			@include media-breakpoint-up(sm) {
				content: '';
				display: block;
				width: 100%;
				height: 42px;
				background: $gradient;
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				z-index: -1;
			}

			@include media-breakpoint-up(md-extra) {
				content: none;
			}
		}

		&__main-content {
			position: relative;
			background-repeat: no-repeat;
			background-size: 300px;
			background-position: bottom 30px center;
			padding: 40px 0 200px;
			z-index: 5;

			@include media-breakpoint-up(sm) {
				padding: 70px 0 40vw;
				background-size: 60%;
				background-position: bottom 50px center;
			}

			@include media-breakpoint-up(md-extra) {
				padding: 80px 0;
				background-size: 50%;
				background-position: left 10% center;
			}

			@include media-breakpoint-up(lg) {
				padding: 100px 0;
			}

			@include media-breakpoint-up(xl) {
				background-position: left 30% center;
			}

			@include media-breakpoint-up(xxl) {
				padding: 120px 0 185px;
			}
		}

		&__block {
			position: relative;
			max-width: 585px;
			margin: 0 auto;

			@include media-breakpoint-up(md-extra) {
				max-width: 100%;
				margin: 0;
			}

			&:before {
				content: '';
				display: block;
				height: 90%;
				background: $gradient;
				opacity: 0.2;
				position: absolute;
				z-index: -1;
				bottom: -70px;
				left: 50px;
				right: 40px;
				filter: blur(42px);
				border-radius: $border-radius-2xl;

				@include media-breakpoint-up(md-extra) {
					left: -60px;
					right: auto;
					width: 90%;
				}
			}
		}

		&__block-inner {
			position: relative;
			z-index: 5;
			background-color: $white;
			border-radius: $border-radius-2xl;
			padding: 25px;

			@include media-breakpoint-up(sm) {
				padding: 60px 40px 40px;
			}
		}

		&__title {
			@include media-breakpoint-down(md-extra) {
				font-size: 32px;
			}
		}

		&__sub-title {
			margin-bottom: 30px;

			@include media-breakpoint-up(lg) {
				margin-bottom: 50px;
			}
		}
	}

	&-default {
		padding: 10px 0 60px;

		@include media-breakpoint-up(sm) {
			padding-bottom: 120px;
		}

		@include media-breakpoint-up(md-extra) {
			padding-top: 30px;
		}
	}

	&-contact-us {
		position: relative;
		padding: 30px 0;

		@include media-breakpoint-up(sm) {
			padding: 45px 0;
		}

		@include media-breakpoint-up(xl) {
			padding: 70px 0;
		}

		&__text-col {
			padding: 35px 0;

			@include media-breakpoint-up(sm) {
				padding: 70px 0 30px;
			}

			@include media-breakpoint-up(md-extra) {
				position: relative;
				padding: 80px 0 150px;
				margin-top: 50px;
			}

			@include media-breakpoint-up(lg) {
				padding-bottom: 80px;
			}

			@include media-breakpoint-up(xl) {
				padding: 100px 0;
				margin-top: 0;
			}

			@include media-breakpoint-up(xxl) {
				padding: 115px 0 135px;
			}

			&:after {
				content: '';
				position: absolute;
				top: 0;
				left: -100vw;
				width: 300vw;
				height: 60%;
				display: block;
				background: $gradient;
				pointer-events: none;

				@include media-breakpoint-up(sm) {
					height: 78%;
				}

				@include media-breakpoint-up(md-extra) {
					bottom: 0;
					height: auto;
				}
			}
		}

		&__form-col {
			position: relative;
			z-index: 5;
		}

		&__title-wrap {
			position: relative;
			z-index: 5;
		}
	}

	&-map {
		padding: 30px 0;

		@include media-breakpoint-up(sm) {
			padding: 45px 0;
		}

		@include media-breakpoint-up(md-extra) {
			padding-bottom: 115px;
		}

		@include media-breakpoint-up(lg) {
			padding-bottom: 100px;
		}

		@include media-breakpoint-up(xl) {
			padding-top: 70px;
		}

		+ .section-become-part {
			@include media-breakpoint-down(lg) {
				margin-top: -200px;
				z-index: 5;
			}
		}

		&__wrap {
			position: relative;
		}

		&__map-holder {
			position: absolute;
			left: -16px;
			right: -16px;
			bottom: 0;
			height: 520px;

			@include media-breakpoint-up(sm) {
				left: -32px;
				right: -32px;
			}

			@include media-breakpoint-up(lg) {
				top: 0;
				left: auto;
				right: 0;
				width: 50%;
				height: auto;
			}

			@include media-breakpoint-up(xl) {
				width: 60%;
			}

			@include media-breakpoint-up(xxl) {
				width: 70%;
			}

			&:before {
				content: '';
				display: block;
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				height: 150px;
				z-index: 5;
				background: linear-gradient(to bottom, $body-bg, rgba($body-bg, 0));
				pointer-events: none;

				@include media-breakpoint-up(lg) {
					width: 300px;
					height: auto;
					bottom: 0;
					right: auto;
					background: linear-gradient(to right, $body-bg, rgba($body-bg, 0));
				}

				@include media-breakpoint-up(xl) {
					width: 400px;
				}

				@include media-breakpoint-up(xxl) {
					width: 500px;
				}
			}

			&:after {
				@include media-breakpoint-down(lg) {
					content: '';
					display: block;
					position: absolute;
					bottom: 0;
					left: 0;
					right: 0;
					height: 200px;
					z-index: 5;
					background: linear-gradient(to top, $body-bg, rgba($body-bg, 0));
					pointer-events: none;
				}
			}
		}

		&__main-content {
			position: relative;
			z-index: 5;
			padding: 0 0 450px;

			@include media-breakpoint-up(lg) {
				padding: 50px 0;
			}

			@include media-breakpoint-up(xl) {
				padding: 70px 0 100px;
			}
		}

		&__contacts-block {
			background-color: $white;
			box-shadow: $box-shadow-sm;
			padding: 25px 16px;
			margin: 0 -16px;

			@include media-breakpoint-up(sm) {
				margin: 0;
				padding: 35px;
			}

			@include media-breakpoint-up(lg) {
				padding: 50px;
			}
		}
	}

	&-platform-welcome {
		background: $gradient;

		&__wrap {
			position: relative;
			background-color: $body-bg;
			border-radius: 0px 0px 16px 16px;
			padding: 0 0 350px;

			@include media-breakpoint-up(sm) {
				padding: 0 0 200px;
			}

			@include media-breakpoint-up(md) {
				padding: 0 0 300px;
				border-radius: 0px 0px 67px 67px;
			}

			@include media-breakpoint-up(md-extra) {
				padding: 80px 0 180px;
			}

			@include media-breakpoint-up(lg) {
				padding: 100px 0 250px;
			}

			@include media-breakpoint-up(xl) {
				padding: 150px 0 350px;
			}

			@include media-breakpoint-up(xxl) {
				padding: 235px 0 490px;
			}
		}

		&__bg {
			position: absolute;
			left: 0;
			right: 0;
			bottom: 20px;
			width: 100%;
			height: 330px;
			background-repeat: no-repeat;
			background-position: right center;
			background-size: contain;

			@include media-breakpoint-up(sm) {
				left: auto;
				top: 0;
				width: 60%;
				height: auto;
				bottom: 100px;
			}

			@include media-breakpoint-up(xl) {
				bottom: 200px;
			}

			&:after {
				@include media-breakpoint-up(sm) {
					content: '';
					display: block;
					width: 40%;
					position: absolute;
					top: 0;
					left: 0;
					bottom: 0;
					background: linear-gradient(to right, $body-bg, rgba($body-bg, 0));
					pointer-events: none;
				}
			}
		}

		&__main-content {
			position: relative;
			z-index: 5;
		}
	}

	&-solution {
		background: $gradient;
		padding: 55px 0;

		@include media-breakpoint-up(lg) {
			padding: 65px 0;
		}

		&.with-top-shift {
			padding-top: 0;

			.section-solution__wrap {
				position: relative;
				z-index: 5;
				margin-top: -60px;

				@include media-breakpoint-up(sm) {
					margin-top: -100px;
				}

				@include media-breakpoint-up(xl) {
					margin-top: -135px;
				}
			}
		}

		&__cards-block {
			margin-bottom: 25px;

			@include media-breakpoint-up(sm) {
				margin-bottom: 40px;
			}

			@include media-breakpoint-up(xl) {
				margin-bottom: 110px;
			}
		}

		&__text {
			@include media-breakpoint-down(sm) {
				font-size: 12px !important;
			}
		}
	}

	&-benefits {
		background: $gradient;

		&__wrap {
			border-radius: 16px 16px 0px 0px;
			background-color: $body-bg;
			padding: 40px 0;

			@include media-breakpoint-up(sm) {
				padding: 60px 0;
			}

			@include media-breakpoint-up(md-extra) {
				padding: 80px 0 120px;
			}

			@include media-breakpoint-up(lg) {
				padding: 100px 0 150px;
			}

			@include media-breakpoint-up(md) {
				border-radius: 67px 67px 0px 0px;
			}

			@include media-breakpoint-up(xxl) {
				padding: 150px 0 195px;
			}
		}

		&__shift-card {
			@include media-breakpoint-up(md-extra) {
				transform: translate(0, 40px);
			}

			@include media-breakpoint-up(lg) {
				transform: translate(0, 75px);
			}
		}
	}

	&-functionality {
		position: relative;
		padding: 40px 0;
		overflow: hidden;

		@include media-breakpoint-up(md-extra) {
			padding: 60px 0;
		}

		@include media-breakpoint-up(lg) {
			padding: 80px 0;
		}

		@include media-breakpoint-up(xl) {
			padding: 100px 0;
		}

		@include media-breakpoint-up(xxl) {
			padding: 190px 0 140px;
		}

		&__bg {
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			width: 100%;
			height: 80%;
			background: $gradient;
			pointer-events: none;

			@include media-breakpoint-up(sm) {
				height: 75%;
			}

			@include media-breakpoint-up(md-extra) {
				width: 85%;
				height: auto;
			}

			@include media-breakpoint-up(lg) {
				width: 75%;
			}

			@include media-breakpoint-up(xl) {
				width: 55%;
			}

			&:after {
				content: "";
				position: absolute;
				top: 100%;
				left: 50%;
				bottom: 0;
				width: 300vw;
				height: 500px;
				transform: translate(-50%, -100px) rotate(-10deg);
				background-color: $body-bg;
				pointer-events: none;

				@include media-breakpoint-up(sm) {
					height: 500px;
					transform: translate(-50%, -150px) rotate(-15deg);
				}

				@include media-breakpoint-up(md-extra) {
					top: 50%;
					left: 100%;
					width: 100vw;
					height: 5000px;
					transform: translate(-150px, -50%) rotate(-15deg);
				}
			}
		}

		&__wrap {
			position: relative;
			z-index: 5;
		}

		&__grid-row {
			> * {
				&:nth-last-child(-n + 2) {
					> * {
						@include media-breakpoint-up(xl) {
							margin-bottom: 0;
						}
					}
				}
			}
		}

		&__text-content {
			margin-bottom: 30px;

			@include media-breakpoint-up(md-extra) {
				padding-right: 30px;
				margin-bottom: 0;
			}

			@include media-breakpoint-up(lg) {
				padding-right: 60px;
			}
		}
	}

	&-about-welcome {
		padding: 40px 0;

		@include media-breakpoint-up(lg) {
			padding: 50px 0;
		}

		&__title {
			@include media-breakpoint-up(xxl) {
				font-size: 95px;
			}
		}

		&__title-holder {
			margin-bottom: 70px;
		}

		&__team-block {
			position: relative;
			background: $gradient;

			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				border-bottom: 150px solid $body-bg;
				border-right: 100vw solid transparent;

				@include media-breakpoint-up(sm) {
					border-bottom-width: 250px;
				}
			}
		}

		&__team-block-inner {
			position: relative;
			z-index: 5;
		}

		&__team-img-wrap {
			display: flex;
			justify-content: center;

			@include media-breakpoint-up(sm) {
				display: block;
			}
		}

		&__team-img {
			width: 680px;
			max-width: inherit;
			margin: -30px 0 20px;

			@include media-breakpoint-up(sm) {
				width: 100%;
				max-width: 100%;
				margin-bottom: 40px;
			}
		}

		&__text-block {
			background-color: rgba($white, 0.95);
			backdrop-filter: blur(7px);
			-webkit-backdrop-filter: blur(7px);
			padding: 15px;

			@include media-breakpoint-up(sm) {
				padding: 30px;
			}

			@include media-breakpoint-up(md) {
				padding: 70px;
			}

			@include media-breakpoint-up(lg) {
				padding: 50px 70px;
			}
		}
	}

	&-image-and-text {
		padding: 40px 0 60px;

		@include media-breakpoint-up(lg) {
			padding: 50px 0 100px;
		}

		@include media-breakpoint-up(xl) {
			background-image: url("../img/bg-lines.svg");
			background-repeat: no-repeat;
			background-position: top -400px right;
			background-size: 400px;
		}

		@include media-breakpoint-up(xxl) {
			padding-bottom: 140px;
			background-size: 600px;
			background-position: top -650px right;
		}

		&__list {
			> * {
				&:not(:last-child) {
					margin-bottom: 40px;

					@include media-breakpoint-up(sm) {
						margin-bottom: 50px;
					}

					@include media-breakpoint-up(xl) {
						margin-bottom: 95px;
					}
				}

				&:nth-child(2n + 1) {
					@include media-breakpoint-up(md-extra) {
						flex-direction: row-reverse;
					}
				}
			}
		}
	}

	&-our-team {
		padding: 100px 0;

		&__title-holder {
			margin-bottom: 75px;
		}

		&__about-block {
			background-color: $white;
			backdrop-filter: blur(27.1828px);
			-webkit-backdrop-filter: blur(27.1828px);
			box-shadow: $box-shadow;
			padding: 50px 50px 25px 50px;
		}
	}
}