@import "variables";

@font-face {
  font-family: '#{$icomoon-font-family}';
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?125uk9');
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?125uk9#iefix') format('embedded-opentype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?125uk9') format('truetype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?125uk9') format('woff'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?125uk9##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icomoon-"], [class*=" icomoon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icomoon-location {
  &:before {
    content: $icomoon-location; 
  }
}
.icomoon-email {
  &:before {
    content: $icomoon-email; 
  }
