.countdown {
	color: $white;

	&__item {
		display: flex;
	}

	&__cell {
		position: relative;
		flex-grow: 1;
		padding: 0 5px;
		text-align: center;

		@include media-breakpoint-up(xl) {
			padding: 0 10px;
		}

		&:first-child {
			padding-left: 0;
			text-align: left;

			@include media-breakpoint-down(xl) {
				border-right: 1px solid rgba($black, 0.1);
			}
		}

		&:last-child {
			padding-right: 0;
		}

		&:not(:last-child) {
			@include media-breakpoint-up(xl) {
				border-right: 1px solid rgba($black, 0.1);
			}
		}

		&:not(:first-child):not(:last-child) {
			.countdown__value {
				position: relative;

				&:before,
				&:after {
					@include sizeElement(4px);

					position: absolute;
					top: 50%;
					right: -7px;
					content: '';
					display: block;
					background-color: $white;

					@include media-breakpoint-up(xl) {
						content: none;
					}
				}

				&:before {
					transform: translate(0, -5px);
				}

				&:after {
					transform: translate(0, 5px);
				}
			}
		}
	}

	&__title {
		font-size: 16px;
		margin-bottom: 5px;

		@include media-breakpoint-up(xl) {
			font-size: 22px;
			margin-bottom: 0;
		}
	}

	&__value {
		color: inherit;
		margin-bottom: 0;
		line-height: 1;
		font-size: 32px;

		@include media-breakpoint-up(xl) {
			font-size: 56px;
		}
	}

	&__progress {
		position: relative;
		margin-top: 20px;
		height: 6px;
		border-radius: 6px;
		background-color: rgba($white, 0.2);
		overflow: hidden;

		@include media-breakpoint-up(xl) {
			margin-top: 10px;
		}
	}

	&__progress-inner {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		border-radius: 6px;
		background-color: $white;
	}
}